.continent {
  height: 500px;
  background-color: lightcyan;
  position: relative;
  &__region {
    position: absolute;
    border-width: 3px;
    border-radius: 10px;
    border-style: solid;
    border-color: rgba(0, 0, 0, 0.3);
    &--hovered {
      border-style: dashed;
    }
    &--edited {
      border-style: dotted;
      border-color: black;
    }
  }
}
