@import 'bootstrap';

.modal {
  position: fixed;
  background-color: rgba(208, 208, 208, 0.75);
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 999;
  opacity: 0;
  pointer-events: none;
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  transition: all 0.3s;
  font-family: $font-family-sans-serif;

  &--visible {
    opacity: 1;
    pointer-events: auto;
  }

  &__wrapper {
    width: 80%;
    max-width: 300px;
    position: relative;
    margin: 10% auto;
    padding: 10px;
    background: white;
  }

  &__heading {
    text-align: center;
    margin: 15px 10px 20px;
    color: black;
    font-size: 16px;
    line-height: 16px;
  }

  &__buttons {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
  }

  &__button {
    padding: 8px 12px;
    text-transform: uppercase;
    background-color: lightgrey;
    color: black;
    font-size: 14px;
    line-height: 14px;
    &:hover {
      background-color: darkgrey;
    }
  }
}
