@import 'bootstrap';

.land-form {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 5px;
  border: 2px solid transparent;
  border-radius: 5px;
  font-family: $font-family-sans-serif;
  color: lightgray;
  margin-bottom: 5px;

  &--hovered {
    border-style: dashed;
    border-color: darkolivegreen;
  }
  &--overlapping {
    border-style: solid;
    border-color: red;
    &.land-form--hovered {
      border-style: dashed;
    }
  }
  &--edited {
    border-style: dotted;
    border-color: black;
  }

  &__action {
    background-color: transparent;
    margin-left: 10px;
  }

  &__input {
    width: 3em;
    display: inline-block;
    text-align: center;
    color: black;
    border: 1px solid darkgrey;
    margin: 0 0.25em;
    &--readonly {
      color: darkgrey;
      border-color: transparent;
    }
    &--error {
      border-color: red;
    }
  }
}
